:root {
  --color-black: #000;
  --color-heavy-metal: #1d1d1b;
  --color-silver: #aeaeae;
  --color-white: #fff;
  --color-red: #ff0000;
}

@font-face {
  font-family: "IBM Plex Mono Light";
  src: url(./../../assets/fonts/IBMPlexMono/IBMPlexMono-Light.ttf) format("truetype");
  font-display: swap;
}
:root {
  font-family: "IBM Plex Mono Light", sans-serif;
}

.footer {
  height: 90px;
  color: var(--color-white);
  background-color: var(--color-black);
}
.footer .copyright {
  display: flex;
  align-items: center;
  height: 100%;
  font-size: 14px;
  line-height: 18px;
}

.navbar {
  display: flex;
  flex-direction: row;
  height: 70px;
}
.navbar .nav {
  flex: 1;
  background-color: var(--color-silver);
}
.navbar .navbar-brand {
  display: block;
  width: 35px;
  height: 35px;
}
.navbar .menu {
  display: flex;
  flex-wrap: wrap;
  margin-left: -20px;
}
.navbar .menu-item {
  margin-left: 14px;
  margin-right: 14px;
}
.navbar .menu-link {
  padding: 4px;
  color: var(--color-white);
  font-size: 18px;
  line-height: 24px;
}
.navbar .language-switcher {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 140px;
}
.navbar .language-switcher a {
  color: var(--color-red);
  font-size: 20px;
  font-weight: bold;
  line-height: 24px;
}

body {
  margin: 0;
}

a {
  text-decoration: none;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  max-width: 1640px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: 20px;
  margin-right: 20px;
}

.col {
  flex: 1 0 0%;
  padding-left: 20px;
  padding-right: 20px;
}

.justify-content-center {
  justify-content: center;
}

.align-items-center {
  align-items: center;
}

.w-100 {
  width: 100%;
}

.h-100 {
  height: 100%;
}